<style lang="css">
</style>
<template lang="html">
  <div class="apply">
    <h4 class="title"></h4>
    <div class="main-apply">
        <div class="row">
          <div class="input-box col-xs-4">
            <p></p>
            <div class="input-group w50">
              <input type="text" class="form-control" name="" placeholder="">
              <span class="input-group-btn">
                <button class="btn btn-default" type="button"></button>
              </span>
            </div>
          </div>
          <div class="input-box col-xs-4">
            <p></p>
            <div class="input-group w50">
              <input type="text" class="form-control" name="" placeholder="">
              <span class="input-group-btn">
                <button class="btn btn-default" type="button"></button>
              </span>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="input-box col-xs-4">
            <p></p>
            <div class="input-group">
              <input type="text" name="" class="form-control w30" value="" readonly>
              <input type="text" name="" class="form-control w30" value="" readonly>
            </div>
          </div>
          <div class="input-box col-xs-4">
            <p></p>
            <div class="input-group">
              <input type="text" name="" class="form-control w30" value="" readonly>
              <input type="text" name="" class="form-control w30" value="" readonly>
            </div>
          </div>
        </div>
        <tablelist :titles="tableTitles" :trdatas="trDataArr" :suns="sunResult" @increment="saveBtn"></tablelist>
        <div class="table-box">
          <table class="table table-striped">
            <thead>
              <tr>
                <th v-for='titleBox in tableBoxTitles'>{{ titleBox }}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td v-for='boxTrTxt in boxTrDataArr'>{{ boxTrTxt }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <th v-for='sun in suns'>{{ sun }}</th>
              </tr>
            </tfoot>
          </table>
        </div>
        <div class="btn-box" role="group">
          <button type="button" class="btn btn-default"></button>
          <button type="button" class="btn btn-default"></button>
          <button type="button" class="btn btn-default"></button>
          <button type="button" class="btn btn-default" @click="selectProduct"></button>
          <button type="button" class="btn btn-default"></button>
          <button type="button" class="btn btn-default"></button>
        </div>
    </div>
    <tabledialog></tabledialog>
  </div>
</template>

<script>
import tablelist from '../components/table-List'
import tabledialog from './table-dialog'
export default {
  name:'apply',
  data () {
    return {
      tableTitles:['','','',''],
      trDataArr:[31553,2,3,1],
      sunResult:['','',0,''],
      tableBoxTitles:['','','','','',''],
      boxTrDataArr:['001','1212122','31553','10000','3','9997'],
      suns:['','','',10000,3,9997]
    }
  },
  components: {
    tablelist,
    tabledialog
  },
  mounted(){
  },
  methods:{
    selectProduct(){
      this.$store.commit('toggleSelectDialog')
    }
  }
}
</script>



